import React from "react";
import Label from "./Label";
import InputElement from "./InputElement";
//import SelectElement from "./SelectElement";

class AccessibilityPID extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    //refs
    //bindings
  }

  render() {
    return (
      <div className="optionsSettings" id="options-box-access" display="none">
        <Label className={"optionTitle"} text={"Jsem cestujc"}></Label>
        <InputElement element={"input"} type={"radio"} name={"transportRequest"} className={"traveler_type"} id={"standard"} text={"Bez dalch poteb"} defaultChecked={true}></InputElement>
        <InputElement element={"input"} type={"radio"} name={"transportRequest"} className={"traveler_type"} id={"slow_walker"} text={"S korkem"} defaultChecked={false}></InputElement>
        <InputElement element={"input"} type={"radio"} name={"transportRequest"} className={"traveler_type"} id={"wheelchair"} text={"S vozkem"} defaultChecked={false}></InputElement>
        <InputElement element={"input"} type={"radio"} name={"transportRequest"} className={"traveler_type"} id={"luggage"} text={"S tkmi zavazadly"} defaultChecked={false}></InputElement>
        <InputElement element={"input"} type={"radio"} name={"transportRequest"} className={"traveler_type"} id={"cyclist"} text={"S kolem do MHD"} defaultChecked={false}></InputElement>
      </div>
    );
  }
}

export default AccessibilityPID;
