import React from "react";
import mapboxgl from "mapbox-gl";

/**
 * Komponenta Mapy, vyuvajc knihovnu MapboxGL
 */
class Map extends React.Component {
  constructor(props) {
    super(props);
    /**
     * State komponenty
     * mapbox - komponenta si udruje aktuln mapbox (mapu), se kterou lze interagovat a odkazovat se na ni
     * ...Marker - je marker hlavnch pozic trasy na map
     * transferPopups - markery zvrazujc msta pestupu na map
     * route - objekty trasy sloen z koordint GPS
     * routeLayers - kad st trasy m jin vlastnosti, kad z nich tedy mus bt samostatnou vrstvou
     */
    this.state = {
      mapbox: undefined,
      startMarker: undefined,
      viaMarker: undefined,
      endMarker: undefined,
      transferPopups: [],
      route: undefined,
      routeLayers: undefined
    };
    //Bindings
    this.addTransferPopup = this.addTransferPopup.bind(this);
    this.clearTransferPopups = this.clearTransferPopups.bind(this);
    this.clearPath = this.clearPath.bind(this);
    this.addMarker = this.addMarker.bind(this);
    this.addPath = this.addPath.bind(this);
  }

  /**
   * Vykresl pestupov marker na map na zadanch souadnicch GPS
   * @param {*} coords 
   */
  addTransferPopup(coords) {
    var map = this.state.mapbox;
    var popups = [];
    if (this.state.transferPopups.length !== 0) {
      for (let i = 0; i < this.state.transferPopups.length; i++) {
        this.state.transferPopups[i].remove();
      }
    }
    if (coords !== undefined && coords[0][0] !== undefined && coords[0][1] !== undefined) {
      for (let i = 0; i < coords.length; i++) {
        var popup = new mapboxgl.Popup({"className":"transferPopup"}).setLngLat([coords[i][0], coords[i][1]]).setHTML("Pestup").addTo(map);
        popups.push(popup);
      }
    }
    this.setState({transferPopups: popups})
  }

  /**
   * Vyist pestupn markery
   */
  clearTransferPopups() {
    if (this.state.transferPopups.length !== 0) {
      for (let i = 0; i < this.state.transferPopups.length; i++) {
        this.state.transferPopups[i].remove();
      }
    }
  }

  /**
   * Pid hlavn markery trasy na mapu na zadanch pozicch GPS
   * Type uruje zda jde o start, prjezd, cl
   * Place je nzev msta/pozice
   */
  addMarker(lon, lat, type, place) {
    // Prvn dojde k vymazn pedchozch marker
    switch (type) {
      case 0:
        if (this.state.startMarker !== undefined) {
          this.state.startMarker.remove();
        }
        break;
      case 1:
        if (this.state.viaMarker !== undefined) {
          this.state.viaMarker.remove();
        }
        break;
      case 2:
        if (this.state.endMarker !== undefined) {
          this.state.endMarker.remove();
        }
        break;
      case 3:
        if (this.state.viaMarker !== undefined) {
          this.state.viaMarker.remove();
        }
        break;
      default:
        break;
    }
    // Pro interakci s mapou se nastav tento mapbox
    var map = this.state.mapbox;
    // Pidn markeru na zadan msta, type uruje zda jde o start, prjezd i cl.
    if (lon !== undefined && lat !== undefined) {
      switch (type) {
        case 0:
          var info = new mapboxgl.Popup({offset:30}).setText("Zatek trasy: "+place);
          var marker = new mapboxgl.Marker({"color":"#ff0000"}).setLngLat([lon, lat]).setPopup(info).addTo(map);
          this.setState({ startMarker: marker });
          break;
        case 1:
          info = new mapboxgl.Popup({offset:30}).setText("Prjezd pes: "+place);
          marker = new mapboxgl.Marker({"color":"#fff400"}).setLngLat([lon, lat]).setPopup(info).addTo(map);
          this.setState({ viaMarker: marker });
          break;
        case 2:
          info = new mapboxgl.Popup({offset:30}).setText("Cl trasy: "+place);
          marker = new mapboxgl.Marker({"color":"#00abff"}).setLngLat([lon, lat]).setPopup(info).addTo(map);
          this.setState({ endMarker: marker });
          break;
        default:
          break;
      }
    }
    // Obnoven stavu mapy na aktualizovanou verzi
    this.setState({ mapbox: map });
  }

  /**
   * Metoda k vyitn mapy. Smae vykreslenou trasu.
   * Postupn odebr vechny vrstvy.
   */
  clearPath() {
    var map = this.state.mapbox;
    if (this.state.routeLayers !== undefined) {
      for (let i = 0; i < this.state.routeLayers; i++) {
        var layerId = "path_layer_"+i;
        map.removeLayer(layerId);
        map.removeSource(layerId);
      }
      this.setState({mapbox: map, routeLayers: 0});
    }
  }

  /**
   * Metoda pro vykreslen trasy
   * 
   * @param {*} listCoords - seznam koordint
   * @param {*} numLayers - poet vrstev, kter se maj vykreslit
   * @param {*} colors - poadovan barvy kad sti trasy
   */
  addPath(listCoords, numLayers, colors) {
    var map = this.state.mapbox;
    map.flyTo({center: [listCoords[0][0][0], listCoords[0][0][1]]});
    this.state.startMarker.togglePopup();
    this.state.endMarker.togglePopup();
    for (let i = 0; i < numLayers; i++) {
      var layerId = "path_layer_"+i;
      map.addLayer({
        id: layerId,
        type: "line",
        source: {
          type: "geojson",
          data: {
            type: "Feature",
            properties: {},
            geometry: {
              type: "LineString",
              coordinates: listCoords[i]
            }
          }
        },
        layout: {
          "line-join": "round",
          "line-cap": "round"
        },
        paint: {
          "line-color": colors[i] !== undefined ? colors[i] : "#888",
          "line-width": 6
        }
      });
    }
    this.setState({ mapbox: map });
    this.setState({ routeLayers: numLayers });
  }

  /**
   * Pi vykreslen komponenty se vytvo nov mapa, kter se zape do state komponenty Map
   * Mapa je centrovna na sted Prahy
   */
  componentDidMount() {
    mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_TOKEN;
    var map = new mapboxgl.Map({
      container: this.mapContainer,
      style: "mapbox://styles/mapbox/streets-v11",
      center: [14.42076, 50.08804],
      zoom: 12
    });
    map.addControl(new mapboxgl.NavigationControl());
    this.setState({ mapbox: map });
  }

  /**
   * Ped odstrannm komponenty z vykreslen na aktuln strnce se smae aktuln mapbox ze state
   */
  componentWillUnmount() {
    if (this.state.mapbox !== undefined) {
      this.state.mapbox.remove();
    }
  }

  /**
   * Vykreslen mapy nvratem prvku div, kter mapu obsahuje
   */
  render() {
    return <div id="map" ref={el => (this.mapContainer = el)} />;
  }
}

export default Map;
