import React from "react";
import { Route, Switch } from "react-router-dom";
import "../css/App.css";
import Planner from "./Planner";
import PlannerWizard from "./PlannerWizard";
import Result from "./Result";
import ResultWizard from "./ResultWizard";
import PlannerContext from "./PlannerContext";
import ResultContext from "./ResultContext";

/**
 * Hlavn komponenta aplikace. Udruje v sob parametry vyhledvn, kter zadal uivatel. 
 * Parametry si pak pevezme komponenta Result, kter uivateli vykresl vsledky.
 * Komponenta obsahuje metody, kter pijmaj v argumentu data z komponent o rove v.
 */
class App extends React.Component {
  constructor(props) {
    super(props);
    /** Bindings, aby komponenta pistupovala ke svm provzanm metodm */
    this.writeStart = this.writeStart.bind(this);
    this.writeVia = this.writeVia.bind(this);
    this.writeEnd = this.writeEnd.bind(this);
    this.writeStartId = this.writeStartId.bind(this);
    this.writeViaId = this.writeViaId.bind(this);
    this.writeEndId = this.writeEndId.bind(this);
    this.writeTimeAndDate = this.writeTimeAndDate.bind(this);
    this.writeOptions = this.writeOptions.bind(this);
    this.writeApi = this.writeApi.bind(this);
    /** Vnitn state komponenty */
    this.state = {
      start: "",
      via: "",
      end: "",
      startId: undefined,
      viaId: undefined,
      endId: undefined,
      timedate: "",
      api: "pid",
      options: ""
    };
  }

  /** Zpis poten pozice vyhledvn do state */
  writeStart(from) {
    this.setState({ start: from });
  }

  /** Zpis prjezdn pozice vyhledvn do state */
  writeVia(thru) {
    this.setState({ via: thru });
  }

  /** Zpis koncov pozice vyhledvn do state */
  writeEnd(to) {
    this.setState({ end: to });
  }

  /** Poadavek na Navitia.io, kter zape ID patc vyhledvan stringov hodnot poten pozice do state komponenty.
   * V metod je kontrola, aby lo o zastvku a ne jin msto.
   */
  writeStartId(place) {
    var url = "https://api.navitia.io/v1/coverage/cz/places?q=" + place;
    console.log(url);
    var headers = new Headers();
    headers.set(
      "Authorization",
      "Basic " + btoa(process.env.REACT_APP_NAVITIA_TOKEN)
    );
    fetch(url, { method: "GET", headers: headers })
      .then(response => response.json())
      .then(json => {
        if (json.places !== undefined) {
          for (let i = 0; i < json.places.length; i++) {
            if (json.places[i].embedded_type === "stop_area") {
              this.setState({ startId: json.places[i].id });
              break;
            }
          }
        }
      });
  }

   /** Poadavek na Navitia.io, kter zape ID patc vyhledvan stringov hodnot prjezdn pozice do state komponenty.
   * V metod je kontrola, aby lo o zastvku a ne jin msto.
   */
  writeViaId(place) {
    var url = "https://api.navitia.io/v1/coverage/cz/places?q=" + place;
    var headers = new Headers();
    headers.set(
      "Authorization",
      "Basic " + btoa(process.env.REACT_APP_NAVITIA_TOKEN)
    );
    fetch(url, { method: "GET", headers: headers })
      .then(response => response.json())
      .then(json => {
        if (json.places !== undefined) {
          for (let i = 0; i < json.places.length; i++) {
            if (json.places[i].embedded_type === "stop_area") {
              this.setState({ viaId: json.places[i].id });
              break;
            }
          }
        }
      });
  }

   /** Poadavek na Navitia.io, kter zape ID patc vyhledvan stringov hodnot koncov pozice do state komponenty.
   * V metod je kontrola, aby lo o zastvku a ne jin msto.
   */
  writeEndId(place) {
    var url = "https://api.navitia.io/v1/coverage/cz/places?q=" + place;
    console.log(url);
    var headers = new Headers();
    headers.set(
      "Authorization",
      "Basic " + btoa(process.env.REACT_APP_NAVITIA_TOKEN)
    );
    fetch(url, { method: "GET", headers: headers })
      .then(response => response.json())
      .then(json => {
        if (json.places !== undefined) {
          for (let i = 0; i < json.places.length; i++) {
            if (json.places[i].embedded_type === "stop_area") {
              this.setState({ endId: json.places[i].id });
              break;
            }
          }
        }
      });
  }

  /** Zpis asu a data do state komponenty */
  writeTimeAndDate(timedate) {
    this.setState({ timedate: timedate });
  }

  /** Zpis nastavench parametr do state */
  writeOptions(options) {
    this.setState({ options: options });
  }

  /** Zpis aktulnho API do state */
  writeApi(api) {
    this.setState({api: api})
  }

  /**
   * Renderovac metoda kter udruje Router pro pechod mezi jednotlivmi strnkami aplikace pi zmn URL.
   * Kadmu vyhledvai/vsledku (komponent) jsou pedny props jako odkazy na metody tto komponenty,
   * kter pak vyuvaj pro pesun dat ne (do tto komponenty)
   */
  render() {
    return (
      <Switch>
        <Route
          exact
          path="/"
          render={props => (
            <Planner
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          path="/form"
          render={props => (
            <Planner
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          exact
          path="/form_result"
          render={props => (
            <Result
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          exact
          path="/old_wizard"
          render={props => (
            <PlannerWizard
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          exact
          path="/old_wizard_result"
          render={props => (
            <ResultWizard
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          exact
          path="/wizard"
          render={props => (
            <PlannerContext
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
        <Route
          exact
          path="/wizard_result"
          render={props => (
            <ResultContext
              {...props}
              writeStart={this.writeStart}
              writeVia={this.writeVia}
              writeEnd={this.writeEnd}
              writeStartId={this.writeStartId}
              writeViaId={this.writeViaId}
              writeEndId={this.writeEndId}
              writeTimeAndDate={this.writeTimeAndDate}
              writeOptions={this.writeOptions}
              writeApi={this.writeApi}
              planner={this.state}
            />
          )}
        />
      
      </Switch>);
  }
}

export default App;
