import React from "react";
import {Link} from "react-router-dom";

/**
 * Header aplikace.
 * Z headeru se lze navigovat temi typy vyhledvae (formule, kontextovho wizardu a normlnho form-wizardu)
 */
class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    //refs
    //bindings
  }

  render() {
    return (
      <header className="header">
        <label className="headerLabel">Prototyp webovho klienta vyhledvae intermodlnch dopravch spojen.</label>
        <label className="headerLabel"><Link to="/form">Formulov typ</Link>/<Link to="/wizard">Contextov wizard</Link>/<Link to="/old_wizard">Klasick wizard</Link></label>
      </header>
    );
  }
}

export default Header;
