import React from "react";
import { Redirect } from "react-router-dom";
import Map from "./Map";
import Header from "./Header";
import Footer from "./Footer";
import PlannerContextPID from "../api/PlannerContextPID";

/**
 * Komponenta kontextovho wizardu, kter je komuniktorem mezi vyhledvaem (komponenta PlannerContextPID) a jdrem aplikace (komponenta App).
 * Je zodpovdn za vykreslen strnky vetn headeru a footeru a nsledn pesun na strnku s vsledky, kdy dojde k vyhledn.
 */
class PlannerContext extends React.Component {
  constructor(props) {
    super(props);
    /** State komponenty
     * ready - uruje zda je aplikace pipravena k zobrazen vsledk
     */
    this.state = {
      ready: false
    };
    //refs
    this.map = React.createRef();
    //bindings
    this.setApi = this.setApi.bind(this);
    this.getStart = this.getStart.bind(this);
    this.getVia = this.getVia.bind(this);
    this.getEnd = this.getEnd.bind(this);
    this.getOptions = this.getOptions.bind(this);
    this.getTimeAndDate = this.getTimeAndDate.bind(this);
    this.setReady = this.setReady.bind(this);
    this.getStartStopPointId = this.getStartStopPointId.bind(this);
    this.getViaStopPointId = this.getViaStopPointId.bind(this);
    this.getEndStopPointId = this.getEndStopPointId.bind(this);
  }

  /** Nsledujc komponenty slou ke komunikaci s jdrem aplikace a vyhledvaem.
   * Pesouvaj data z vy vrstvy (vyhledva) do vrstvy ni (jdro)
   **/
  setApi(selectedApi) {
    this.props.writeApi(selectedApi);
  }

  getStart(start) {
    this.props.writeStart(start);
  }

  getVia(via) {
    this.props.writeVia(via);
  }

  getEnd(end) {
    this.props.writeEnd(end);
  }

  getStartStopPointId(place) {
    this.props.writeStartId(place);
  }
  getViaStopPointId(place) {
    this.props.writeViaId(place);
  }
  getEndStopPointId(place) {
    this.props.writeEndId(place);
  }

  getOptions(options) {
    this.props.writeOptions(options);
  }

  getTimeAndDate(timedate) {
    this.props.writeTimeAndDate(timedate);
  }

  /** Metoda nastavujic pipravenost aplikace k vyhledn
   * Pokud je state ready true, dojde k pesunu z vyhledvae na strnku s vsledky.
   */
  setReady(bool) {
    this.setState({ ready: bool });
  }

  /**
   * Renderovac metoda kter udruje obsahuje header, footer a vyhledva aplikace.
   * Kadmu vyhledvai (komponent PlannerContextPID) jsou pedny props jako odkazy na metody tto komponenty,
   * kter pak vyuvaj pro pesun dat ne (do tto komponenty).
   * 
   * Pi zmn state ready v tto komponent dojde na redirect na vsledky.
   */
  render() {
    if (this.state.ready) {
      return <Redirect to="/wizard_result"></Redirect>;
    } else {
      return (
        <React.Fragment>
          <Header></Header>
          <main className="journey-planner">
            <section className="planner">
              <PlannerContextPID
                start={this.getStart}
                via={this.getVia}
                end={this.getEnd}
                startId={this.getStartStopPointId}
                viaId={this.getViaStopPointId}
                endId={this.getEndStopPointId}
                timedate={this.getTimeAndDate}
                ready={this.setReady}
                planner={this.props.planner}
                setApi={this.setApi}
                getOptions={this.getOptions}
              ></PlannerContextPID>
            </section>
            <Map ref={this.map}></Map>
          </main>
          <Footer></Footer>
        </React.Fragment>
      );
    }
  }
}

export default PlannerContext;
